import MescrollMixin from '@/uni_modules/mescroll-uni/components/mescroll-uni/mescroll-mixins.js';
import Config from '@/config';
export default {
	mixins: [MescrollMixin],
	data() {
		return {
			appStatusBarHeight: 44,
			mescrollHeight: 0,
			downOption: {
				bgColor: '#f2f2f2',
				auto: false,
				textColor: Config.color.primary,
				textInOffset: this.$t(''),
				textOutOffset: this.$t(''),
				textLoading: this.$t(''),
				textSuccess: this.$t(''), // 
				textErr: this.$t(''),
			},
			upOption: {
				onScroll: false,
				auto: false,
				page: {
					num: 0,
					size: 10,
					time: null,
				},
				empty: {
					use: false,
				},
				noMoreSize: 8,
				textLoading: this.$t(''),
				textNoMore: this.$t(''),
			},
			isNotData: false, // true/false

			total: 0, // 
			pager: {
				pageNum: 1, // 
				pageSize: 10, // 
			},
			list: [],
			method: 'get',
			isGet: true, //  getListFalse
			apiUrl: null, // ,
			apiType: null, // 1 
			query: {
				// 
			},
		};
	},
	onLoad() {
		this.appStatusBarHeight = uni.$u.sys().statusBarHeight + 44;
		this.mescrollHeight = this.$u.addUnit(this.getMescrollHeight(), 'px');

		this.$nextTick(() => {
			this.getList();
		});
	},
	methods: {
		/**
		 * 
		 * @param target - 0  1 
		 */
		async getList(target = 0) {
			this.beforeGetList && this.beforeGetList(); // 
			if (!this.apiUrl) {
				this.mescroll.endSuccess(0, true);
				return;
			}
			console.log('query', this.query);
			let params = {};
			if (this.method === 'get') {
				params = {
					params: {
						...this.query,
						...this.pager,
					},
				};
			} else {
				if (this.apiType === 1) {
					params = {
						...this.query,
					};
				} else {
					params = {
						...this.query,
						...this.pager,
					};
				}
			}
			this.$http[this.method](this.apiUrl, params).then((result) => {
					if (target === 0) {
						this.list = result.data;
						this.total = result.total;
					}

					if (target === 1) {
						this.list = this.list.concat(result.data ?? []);
					}

					// 
					this.isNotData = this.list.length >= this.total;
					this.$ch.sleep(100).then(() => {
						this.mescroll && this.mescroll.endSuccess(result.data.length, !this.isNotData);
					});

					this.afterGetList && this.afterGetList(); // 
				})
				.finally(() => {
					this.isGet = false;
				});
		},
		downCallback() {
			this.pager.pageNum = 1;
			this.getList();
		},
		upCallback() {
			this.pager.pageNum++;
			this.getList(1);
		},
		getMescrollHeight() {
			let heigt = 0;
			heigt += this.appStatusBarHeight;



			// 
			// uni.createSelectorQuery().select(".searchBar").boundingClientRect(function(data) {
			//     console.log('data', data)
			//     if (data && data.height) {
			//         heigt += data.height;
			//     }
			// }).exec();
			return heigt;
		},
	},
};
